                            <span class=" text-black ">{{ formatPrice(item.debt) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.given="{item}">
                        <div class="d-flex">
                            <span class="ml-2 text-black">{{ formatPrice(item.given) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.remained="{item}">
                        <div class="d-flex">
                            <span class=" text-black ">{{ formatPrice(item.remained) }} so`m</span>
                        </div>
                    </template>

                    <template v-slot:item.date="{item}">
                        <div class="d-flex">
                            <span class="ml-2 text-black">{{ item.date }}</span>
                        </div>
                    </template>

                    <template v-slot:item.giveMoney="{item}">
                        <ModalPayment v-if="$can('read', 'finance_qarzdorlik_give_money')"></ModalPayment>
                    </template>

                    <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center justify-center">
                            <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'finance_qarzdorlik_fabrikalar')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1" v-if="$can('delete', 'finance_qarzdorlik_fabrikalar')">
                                <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                            </div>
                        </div>
                    </template>
                </v-data-table>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                    total-visible="7"
                    class="my-2"
                ></v-pagination>
            </v-col>
        </v-row>


        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditModal">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditIncomeThread" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      
                        <!-- <div class="mt-4">
                            <span class="text-black ml-1">Turni tanlang</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'code']}"
                              v-slot="{ errors }">
                                <v-select :items="filteredItems" v-model="product.product_category_id" 
                                    class="mt-2" item-value="id" label="Tanlang"
                                    outlined :error-messages="errors" item-text="name">
                                </v-select>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Nomni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'from_where']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.name" class="mt-2"
                                    placeholder="Misol: Vilvit" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Kodni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'name']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.code" type="number" class="mt-2"
                                    placeholder="Misol: 666777" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Tavsif kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'description']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.description" class="mt-2"
                                    placeholder="Misol: bu yashil ip" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div> -->

                            
                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditModal"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <DeleteModal :deleteModal="deleteModal"></DeleteModal>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import DeleteModal from '@/components/modals/DeleteModal.vue'
import ModalPayment from '@/components/modals/Finance/ModalPayment'

export default {
    mixins: [loaderMixin, priceMixin],
    components: {
        ModalPayment,
        DeleteModal
    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            modalPayment: false,
            editModal: false,
            deleteModal: false,
            headersDebts:[
                {text: 'Kimdan', value: 'where'},
                {text: 'Qarzdorlik', value: 'debt',},
                {text: 'Berildi', value: 'given'},
                {text: 'Qoldi', value: 'remained'},
                {text: 'Dastlabki sana', value: 'date'},
                {text: 'Pul berish', value: 'giveMoney'},
                {text: 'Harakatlar', value: 'actions'},
            ],
            items: [
                {where: 'NEON textile', debt: '1400000', given: '1400000', 
                remained: '1400000', date: '21.09.2021', giveMoney: '500000'},

                {where: 'NEON textile', debt: '1400000', given: '1400000', 
                remained: '1400000', date: '21.09.2021', giveMoney: '500000'},

                {where: 'NEON textile', debt: '1400000', given: '1400000', 
                remained: '1400000', date: '21.09.2021', giveMoney: '500000'},
             ],
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted() {
        await this.refresh()
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState({
            // productCategories: state => state.categories.productCategories,
            // productCodesFields: state => state.code.productCodesFields,
        }),
    },
    methods: {
        ...mapActions(
            {
                // saveProductCode: 'code/saveProductCode',
                // postNewIp: 'ipAndAksessuar/postNewIp'
            }
        ),



       // ============= REFRESH START =============
        async refresh(){
            // await this.fetchProductCodes({status: this.productStatus})
            // this.filteredProducts = this.productCodesFields.data
            // .filter(el => el.product_category_id == 4)
        },
        // ============= REFRESH END =============





        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeEditModal(){
            this.editModal = false
        },
        closeDialog(){
            this.clearInputs();
            this.modalPayment = false
            this.$refs.formExit.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============




        // ============= ON_SUBMIT3 START =============
        async onSubmit(){
            this.$refs.formExit.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= ADDING_NEW_IP START =============
                    let aksessuar = await this.postNewIp(
                        { 
                            created_at: this.date,
                        })

                    if(aksessuar.response && aksessuar.response.status == 422){
                        if(aksessuar.response.data.errors.from_where){
                            this.errorMessage = aksessuar.response.data.errors.from_where[0];
                            this.errorPlace = 'from_where'
                        }else if(aksessuar.response.data.errors.amount){
                            this.errorMessage = aksessuar.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(aksessuar.response.data.errors.price){
                            this.errorMessage = aksessuar.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(aksessuar.response.data.errors.total_price){
                            this.errorMessage = aksessuar.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }else if(aksessuar.response.data.errors.paid){
                            this.errorMessage = aksessuar.response.data.errors.paid[0];
                            this.errorPlace = 'paid'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else{
                        this.$toast.success('Muvaffaqiyatli qo`shildi!');
                        this.closeDialog();
                    }
                    // ============= ADDING_NEW_IP END =============



                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT3 END =============

    },
   

}
</script>
}
</script>

