<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="modalPayment"
        >
            <template v-slot:activator="{ on, attrs }">
                <div class="d-flex ">
                    <div v-bind="attrs" class="d-flex align-center"
                        v-on="on" @click="openWindow">
                        <img src="@/assets/icons/money-send-blue.svg" width="20" alt="money-send">
                        <span style="color: #2886FF;" class="ml-2 fs-17px">Pul berish</span>
                    </div>
                </div>
            </template>
            <template>
                 <v-card class="aksessuar_add">

                    <div class="aksessuar_add__title">
                        <span class="text-black">Pul berish</span>
                        <v-btn icon @click="closeDialog">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formExit" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">

                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>
                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-date-picker
                                        color="blue lighten-1"
                                        v-model="date"
                                        @input="dateMenu = false"
                                        locale="ru-ru"
                                        first-day-of-week="1"
                                        :error-messages="errors"
                                    ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                            </div>

                            <div>
                                <span class="text-black ml-1">Summani kiriting</span>
                                <ValidationProvider :rules="{required: true, 
                                error: [errorStatus, errorMessage, errorPlace, 'total_price']}" 
                                v-slot="{ errors }">
                                    <v-text-field 
                                        outlined :error-messages="errors"
                                        v-model="sum" class="mt-2"
                                        placeholder="Misol: 1,240,000 so’m">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeDialog"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: [''],
    mixins: [dateMixin, loaderMixin],
    components: {

    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            modalPayment: false,
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted() {
        await this.refresh()
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState({
            // productCategories: state => state.categories.productCategories,
            // productCodesFields: state => state.code.productCodesFields,
        }),
    },
    methods: {
        ...mapActions(
            {
                // saveProductCode: 'code/saveProductCode',
                // postNewIp: 'ipAndAksessuar/postNewIp'
            }
        ),



       // ============= REFRESH START =============
        async refresh(){
            // await this.fetchProductCodes({status: this.productStatus})
            // this.filteredProducts = this.productCodesFields.data
            // .filter(el => el.product_category_id == 4)
        },
        // ============= REFRESH END =============





        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog(){
            this.clearInputs();
            this.modalPayment = false
            this.$refs.formExit.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============




        // ============= ON_SUBMIT3 START =============
        async onSubmit(){
            this.$refs.formExit.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= ADDING_NEW_IP START =============
                    let aksessuar = await this.postNewIp(
                        { 
                            created_at: this.date,
                        })

                    if(aksessuar.response && aksessuar.response.status == 422){
                        if(aksessuar.response.data.errors.from_where){
                            this.errorMessage = aksessuar.response.data.errors.from_where[0];
                            this.errorPlace = 'from_where'
                        }else if(aksessuar.response.data.errors.amount){
                            this.errorMessage = aksessuar.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(aksessuar.response.data.errors.price){
                            this.errorMessage = aksessuar.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(aksessuar.response.data.errors.total_price){
                            this.errorMessage = aksessuar.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }else if(aksessuar.response.data.errors.paid){
                            this.errorMessage = aksessuar.response.data.errors.paid[0];
                            this.errorPlace = 'paid'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else{
                        this.$toast.success('Muvaffaqiyatli qo`shildi!');
                        this.closeDialog();
                    }
                    // ============= ADDING_NEW_IP END =============



                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT3 END =============

    },
   

}
</script>


